import bildPortriat1 from '../Img/Animals1.jpg'
import bildPortriat2 from '../Img/Animals2.jpg'
import bildPortriat3 from '../Img/Animals3.jpg'
import styled from 'styled-components'
import device from '../Components/device'
import ScrollToTop from '../Components/ScrollToTop'
import {useHistory} from 'react-router-dom'
import {FaAngleLeft} from 'react-icons/fa'


const PortraitBox = styled.div`
height:80vh;
width:80vw;
position: absolute;
top:50%;
left:50%;
transform:translate(-50%,-50%);
background-color:black;


`
const PortraitBildBox=styled.div`
height:600vh;
width:100%;
background-color:red;
position:relative;


`
const PortraitBild1 = styled.img`
position: absolute;
background-image: url(${bildPortriat1});
background-position: 50% 20%;
height:100%;
width:100%;
background-repeat: no-repeat;

@media ${device.mobile} {
   background-position: center;
   background-size:cover ;
   background-attachment: fixed;
  }


`

const PortraitBild2 = styled.img`
position:absolute;
background-image: url(${bildPortriat2});
background-position: 50% 50%;
height:100%;
width:100%;
top:100vh;
background-repeat: no-repeat;

@media ${device.mobile} {
   background-position: center;
   background-size:cover ;
   background-attachment: fixed;
  }
`
const PortraitBild3 = styled.img`
position:absolute;
background-image: url(${bildPortriat3});
background-position: 50% 50%;
height:100%;
width:100%;
top:200vh;
background-repeat: no-repeat;

@media ${device.mobile} {
   background-position: center;
   background-size:cover ;
   background-attachment: fixed;
  }
`
const BackArrow = styled(FaAngleLeft)`
        position:fixed;
        z-index:120;
        top:92vh;
        left:15%;
        transform:translate(-50%);
        width:90px;
        color:white;
        height:40px;

    &:hover {
        height:50px;
        
        

    }

        `
const Portrait = () => {
    const history =useHistory();
    return (     
 
    <div style={{backgroundColor:"black", zIndex:"101",
    height:"300vh",width:"100%"}} >
 <ScrollToTop/>
    <PortraitBox>
    <PortraitBild1></PortraitBild1>
    <PortraitBild2></PortraitBild2>
    
    <PortraitBild3></PortraitBild3>
    </PortraitBox>
    <BackArrow onClick={()=>history.goBack()}/>
    </div>
 

    )
}
 
export default Portrait;