import styled from 'styled-components'
import React, {useState, useEffect} from 'react'
import bildKasia from '../Img/kasia.jpg'
import obraz from '../Img/obraz1.jpg'




const StyleForm = styled.div`
position: relative;


height:300vh;
width:100%;
background-position: ;

`



const StyleBox = styled.div`
overflow:hidden;

position: fixed;
top:85%;
left:50%;
border-radius: 50px 0 50px  50px;
transform:translate(-50%,-50%);

border: black solid 6px;
width:95%;
background-color: white;
background-color: #DFDBE5;





`


const StyledText = styled.h1`

position:absolute;
overflow:hidden;
text-align:center;
font-size:30px;
width:100%;
top:50%;
transform:translate(0,-50%);
color:black;

`

const StyledText2 =styled.h2`
position:absolute;
top:50%;
left:50%;
transform:translate(-50%,-50%)

`


const Avatar = styled.div`
background-image: url(${bildKasia});
background-position:0px center;
background-size:cover ;
background-repeat: no-repeat ;
background-attachment: fixed;
height:100vh;
width:100%;
position:absolute;
top:0;
left:0;




`
const Avatar1 = styled.div`
background-image: url(${obraz});
background-position:0px center;
background-size:cover ;
background-attachment: fixed;
background-repeat: no-repeat ;


height:1000vh;
width:100%;
position:absolute;
top:100vh;
left:0;
`


const About = () => {

    
        const [prevScrollPos, setPrevScrollPos] = useState(0);
        const [visible, setVisible] = useState(true);

    
    const handleScroll = ()=>{
        const curentScrollPos = window.pageYOffset;
    
    
    setVisible((prevScrollPos>curentScrollPos&&prevScrollPos-curentScrollPos>100)||curentScrollPos<500);
    
    setPrevScrollPos(curentScrollPos);
    };

    useEffect(()=>{
window.addEventListener('scroll', handleScroll);
return()=>window.removeEventListener('scroll', handleScroll);

    }, [prevScrollPos, visible, handleScroll])

    return ( <div>

<StyledText2>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</StyledText2>

        <Avatar style={{position:'sticky',width:visible?"0":"100%",transition:"ease-in .2s"}} />
        <Avatar1 style={{position:'sticky'}} />
        <StyleForm>
            
<StyleBox style={{transition:"ease-in .8s",height: visible?"0":"200px",}}><StyledText style={{fontSize:"20px"}}>Jestem początkującą artysktą, świeżym powiewem, błyskotlwym obserwatorem, dalekowzroczna i niebezpieczna... Jestem Catlens!</StyledText></StyleBox>
</StyleForm>

    </div> );
}
 
export default About;